<template>
	<view class="" style="width: 100vw; height: 100vh;">
		<popUp ref="welcome"></popUp>
		<view class="bgc-o-y" style="position: fixed; bottom: 0; z-index: -30; height: 100vh; width: 750rpx;"></view>
		<view class="">
			<view class="bgc-picture1">
				<image src="../../static/fristPage/GOlogo.png" mode="aspectFill"
				style="width: 100vw; height: 100vh;"></image>
			</view>
			<view class="" style="height: 75vh;"></view>
			<view class="" 
			style="margin: 0 100rpx; border-radius: 20rpx; overflow: hidden; border: 1rpx solid #b7b7b7;">
				<progress 
				style="height: 100%;"
				:percent="percent"  stroke-width=15 backgroundColor="#ffcd43" activeColor="#b7b7b7" />
			</view>
			
			<!-- 更新弹框 -->
			<view class="showmodel" v-show="ifGo">
				<view class="" style="height: 20vh;"></view>
				<view class="bgc_FFF" 
					style="margin: 0 78rpx; border-radius: 51rpx;  border: 1rpx solid #9ec0d7; position: relative;">
					<view v-if="!ifForcedUpdate" class="df-jcc-aic" style="position: absolute; top: 18rpx; right: 18rpx;">
						<image @click="newclose" src="../../static/fristPage/qx.png" mode="widthFix" 
						style="width: 60rpx;"></image>
					</view>  
					<view class="df-jcc-aic" style="margin: 40rpx 0; width: 100%;">
						<image src="../../static/fristPage/Head3.png" mode="widthFix" style="width: 166rpx;"></image>
					</view>
					<view class="ta-c color-H0f649c f49rpx fw800" style="margin: 0 0 50rpx; height: 43rpx; letter-spacing: -3rpx;">
						{{title}}
					</view>
					<view class="f33rpx color-H0f649c fw800 ta-c" style="">
						Features List
					</view>
					<view class="color-H0f649c f33rpx" v-html="content"  style="margin: 15rpx 48rpx 50rpx ;"></view>
					<view class="FFF ba f33rpx ta-c bgc-o-r fw800" @click="goUpdate"
						hover-class="hover_a24" hover-start-time=0 hover-stay-time=150 
						style="line-height: 84rpx; margin: 0 42rpx; height: 84rpx; border-radius: 42rpx; ">
						{{key}} 
					</view>
					
					<view class="" style="height: 20rpx;"></view>
					<view class="FFF ba f33rpx ta-c bgc-o-r fw800" @click="newclose" v-if="!ifForcedUpdate"
						hover-class="hover_a24" hover-start-time=0 hover-stay-time=150 
						style="line-height: 84rpx; margin: 0 42rpx; height: 84rpx; border-radius: 42rpx; ">
						Skip Update
					</view>
					<view class="" style="height: 80rpx;"></view>
				</view>
			</view>	
	  
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				ifLogin:false,
				ifForcedUpdate:false,
				ifGo:false,
				percent:1,
				flags:1, 
				title:'New Version!',
				content:'',
				key:'Download Update',
				network:''
			}
		},
		methods:{
			newclose(){
				this.ifGo = false
				this.Login()
			},
			
			ss(){
				console.log('sd');
			},
			
			wangLuo(){
				let _this = this
			},
			 
			async getAppDate(){
				
				console.log('执行了');
				let _this = this
				// #ifdef APP-PLUS
				plus.screen.lockOrientation("portrait-primary")
				let version= plus.runtime.version 
				let appid = plus.runtime.appid 
				version = version.split('.').join('') /1

				// version = 219
				console.log('手机当前版本：'+version);
				// #endif
				let N_version = 0
				const res = await this.$myRequest({
					url:'/api/app/version' 
				})

				if(res.data.succeeded){
					_this.content = res.data.data.appIosDescription
					_this.ifForcedUpdate = res.data.data.appIosForced 
					N_version = res.data.data.appIosVersion
					N_version = N_version.split('.').join('') /1 
					
					console.log('服务器版本'+N_version);
					if(N_version>version){
						_this.ifGo = true
					}else if(N_version == version){
						_this.ifGo = false
						_this.Login()  
						console.log('s');
					}else{ 
						console.log('错误');
						_this.ifGo = true
						// _this.Login()
					}
				}else{
					_this.Login()
				}
			},
			
			goUpdate(){
				let appleId=1590990417 //app的appleId
				plus.runtime.launchApplication({
					action: `itms-apps://itunes.apple.com/cn/app/id${appleId}?mt=8`
				}, function(e) {
					console.log('Open system default browser failed: ' + e.message);
				});
			},
			
			addPercent(){
				var that = this;
				if(that.percent >= 0 && that.percent <= 100){
					that.percent = that.percent + 0.3
				}else{
					that.percent = 0
				} 
				var timer = setTimeout(function(){
					that.addPercent()
				},50);
			},
			
			async Login(){
				this.addPercent()
				let Password = uni.getStorageSync('Password')
				let email = uni.getStorageSync('Email')
				if(email == ''){
					uni.redirectTo({
						url:"./inputEmail"  
					}) 
					return false
				}else if(Password == ''){
					uni.redirectTo({
						url:"./inputEmail"  
					}) 
					return false
				}
				const res = await this.$myRequest({
					method:'POST',
					url:'/api/auth/login',
					data:{
						"email":email,
						"password":Password
					}
				}) 
				// console.log(res);
				if(res.data.succeeded){
					uni.setStorageSync('token',res.data.data.token)
					uni.setStorageSync('Password',Password)
					uni.setStorageSync('hasActive',res.data.data.hasActive)
					if(res.data.data.active){
						if(res.data.data.isBind){
							uni.setStorageSync('isLogin',true)
							uni.switchTab({
								url:'../index/index'
							})
						}else{
							uni.redirectTo({
								url:'../subscription/Bind'
							})  
						}
					}else{
						if(res.data.data.isBind){
							uni.setStorageSync('isBind',true)

						}else{
							uni.setStorageSync('isBind',false)
						}
						uni.redirectTo({
							url:'../subscription/subscription'
						})
					}
				}else{
					uni.redirectTo({
						url:"./inputEmail"  
					}) 
					return false
				}
			},
		},
		created() {
			// this.addPercent()
			// this.wangLuo()
			// this.getAppDate()
		},
		mounted() {
			// this.wangLuo()
			// console.log('m');
			// if(this.flags == 2){
			// 	this.$nextTick(function(){
			// 		this.$refs.welcome.openPopup('Request out of time, please check network and try it again')
			// 	})
			// }else{
				
			// }
			
		},
		onShow() {
			let _this = this
			// console.log('L');
			uni.getNetworkType({
					success: function (res) {
							console.log(res.networkType);
							if(res.networkType == 'none'){
								// _this.$refs.Welcome.openPopup('无网络')
								console.log('无网络'); 
								// _this.flags = 2
								_this.$nextTick(function(){
									_this.$refs.welcome.openPopup('Request out of time, please check network and try it again')
								})
							}else{
								console.log('有网络');
								// _this.flags = 1
								_this.getAppDate()
							}
					}
			}); 
		},
		onLoad() {

		},
		onHide() {
		}
	}
</script>

<style scoped>
	.ba{
		box-shadow: 0rpx 0rpx 3rpx #000;
	}
	.bgc-picture1{
		 z-index:-22; 
		 width:100vw; 
		 height:100vh; 
		 background-color: #ffcd43;
		 position: fixed; 
		 top:0; 
	}
	.showmodel{
		z-index: 20;
		background: rgba(0,0,0,0.5);
		position: fixed;
		left: 0;
		right: 0;
		bottom: -100rpx; 
		top: 0;
	}
</style>
